//// timeline small
.timeline-sm {
  padding-left: 110px;

  .timeline-sm-item {
    position: relative;
    padding-bottom: 20px;
    padding-left: 40px;
    border-left: 2px solid $gray-300;

    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 3px;
      left: -7px;
      width: 12px;
      height: 12px;
      border-radius: 50%;
      background: $white;
      border: 2px solid $primary;
    }

    .timeline-sm-date {
      position: absolute;
      left: -104px;
    }
  }
}

//
@media (max-width: 420px) {
  .timeline-sm {
    padding-left: 0px;

    .timeline-sm-date {
      position: relative !important;
      display: block;
      left: 0px !important;
      margin-bottom: 10px;
    }
  }
}

@media (max-width: 375.98px) {
  .timeline {
    .timeline-desk {
      width: 52%;
    }
  }
}